
@import "components/buttons";
@import "utilities/headings";
@import "components/forms";
@import "layouts/menu";
@import "layouts/todolist";
@import "layouts/todo";

  *{
  font-family: "Lato", sans-serif;
  margin:0;
  padding:0;
  box-sizing:border-box;
}

html{
font-size:10px;
}

.momcontainer{
  height:3rem;
  width:100%;
  font-size: 1.5rem;
  position:relative;
  margin-left:50%;
  transform:translateX(-50%);
  margin-top:1rem;
  &__menu{
    width:50%;
    margin:0 auto ;
    
   
  }
 
}
.todolist__container{
 display:flex;
 flex-direction: row;
 flex-wrap: wrap;
  padding:20px;
  
  &-main{
    
    position:relative;
    border:1px solid black;
  }
}
.strike {
   text-decoration: line-through;
   cursor: pointer;
}
//INFO

.info{
  margin-left:50%;
  transform: translateX(-50%);
  margin-top:3rem;
  font-size: 1.6rem;
  &__main{}
  &__header{
    margin-bottom:2rem;
    text-align:center;
  }
}
//instructions
.instructions{
  margin-left:50%;
  transform: translateX(-50%);
  margin-top:3rem;
  font-size:1.6rem;
  &__header{
    margin-bottom: 2rem;
    text-align:center;
  }
}